@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
/* fonts */
/* color palette */
:root {
  /* material-ui */
  --primary: #1976d2;
  --secondary: #dc004e;
  /*  */
  --one: rgb(253, 123, 57);
  --two: rgb(238, 114, 60);
  --three: rgb(249, 150, 51);
  --four: rgb(48, 34, 33);
  /*  */
  --lightgray: rgb(250, 252, 254);
}

/* ========================================================= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(250, 252, 254);
  background-color: var(--lightgray);
  /* background-color: var(--four); */

  overflow-x: hidden;
}

button {
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

button:focus {
  outline: none;
}

.cursor {
  width: 2rem;
  height: 2rem;
  border: solid 2px rgb(48, 34, 33);
  border: solid 2px var(--four);
  border-radius: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  pointer-events: none;
  display: none;
}

/* =============================================== */

.top-image {
  width: 100%;
  background-image: url(/music-generator/static/media/music-studio.2176135c.jpg);
  background-size: cover;
  background-position: center;
  height: calc(27vh - 1px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: rgba(255, 255, 255, 0.8);
  font-size: 30px;
}

.thin-line {
  height: 1px;
  background-color: rgb(249, 150, 51);
  background-color: var(--three);
}

/* =============================================== */

.content {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

/* =============================================== */

.side-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  width: 10%;
  height: 100vh;
  padding: 10px 0 0 10px;
  font-size: 14px;
}

.side-menu-container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 10px;
}

.side-menu {
  /* color: rgba(0, 0, 0, 0.8); */
  color: rgba(255, 255, 255, 0.8);
  /* color: var(--three); */
  margin-top: 20px;
  z-index: 10;
}

.menu-bar {
  /* color: rgba(0, 0, 0, 0.9); */
  color: rgba(255, 255, 255, 0.9);
  /* color: var(--three); */
  font-size: 15px;
}

.side-menu > i {
  margin-right: 7px;
  /* color: rgba(0, 0, 0, 0.6); */
  color: rgba(255, 255, 255, 0.8);
  /* color: var(--three); */
}

.svg-container > svg {
  height: 100vh;

  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  stroke: none;
  fill: rgb(249, 150, 51);
  fill: var(--three);
  -webkit-transform: rotate(270deg) scaleX(-1);
          transform: rotate(270deg) scaleX(-1);
}

.svg-container {
  top: 50%;
  left: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: inline-block;
  position: absolute;
  height: 100vh;
  padding-bottom: 100%;
  vertical-align: middle;
}

.svg-container-overlay > svg {
  height: 100vh;

  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  stroke: none;
  fill: rgb(250, 252, 254);
  fill: var(--lightgray);
  -webkit-transform: rotate(270deg) scaleX(-1);
          transform: rotate(270deg) scaleX(-1);
}

.svg-container-overlay {
  top: 50%;
  left: -4px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: inline-block;
  position: absolute;
  height: 100vh;
  padding-bottom: 100%;
  vertical-align: middle;
}

