/* =============================================== */

.music {
  width: 65%;
  padding: 50px 0 0 0;
}

.generator-button-container {
  width: 100%;
  height: 60px;
  position: relative;
}

.generator-button {
  position: absolute;
  top: 50%;
  left: calc((40 / 65) * 100%);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

  background: linear-gradient(
    to bottom right,
    rgb(238, 114, 60),
    rgb(249, 150, 51)) !important;

  background: linear-gradient(
    to bottom right,
    var(--two),
    var(--three)
  ) !important;
  color: white !important;
  height: 60px;
  width: 200px;
}

.generator-button2 {
  background: rgb(56, 51, 48) !important;
  color: white !important;
}

.music-player-container {
  margin-top: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
/*=============================== */

.custom-settings {
  width: 25%;
  height: 100vh;
  padding: 50px 30px 20px 0;
  color: rgba(0, 0, 0, 0.5);
}

.setting-card {
  height: 80%;
  width: 100%;
  background-color: rgb(250, 252, 254);
  background-color: var(--lightgray);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 5px;
  cursor: none;
}

.setting-card * {
  cursor: none;
}

.setting-card:hover {
  border: solid 2px rgb(249, 150, 51);
  border: solid 2px var(--three);
  /* transition: 0.5s; */
}

.selectors {
  display: none;
  cursor: none;
  height: 80%;
  width: 100%;
  background-color: rgb(250, 252, 254);
  background-color: var(--lightgray);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 5px;
  border: solid 2px rgb(249, 150, 51);
  border: solid 2px var(--three);
}

.selectors * {
  margin-top: 20px;
}

.apply-settings {
  height: 20px;
}

.apply-settings:hover {
  color: rgba(0, 0, 0, 0.7);
}

.apply-settings > p {
  font-size: 15px;
}

/* color palette */
:root {
  /*  */
  --pink: rgba(252, 169, 169, 0.6);
  --pink1: #fe8daa;
  --pink2: #dfdbdf;
  --pink3: #fe8daa;
  /*  */
  --one: rgb(253, 123, 57);
  --two: rgb(238, 114, 60);
  --three: rgb(249, 150, 51);
  --four: rgb(48, 34, 33);
  /*  */
  --lightgray: rgb(250, 252, 254);
}

/* ==================================== */

.music-player {
  position: relative;
  height: 50px;
  width: 200px;
  border-radius: 50px;
  background-color: rgb(250, 252, 254);
  background-color: var(--lightgray);
  border: solid 2px rgb(249, 150, 51);
  border: solid 2px var(--three);
  margin: 20px 0;
}

.play-pause-container {
  position: absolute;
  top: 50%;
  left: 12%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 10;
}

.play-pause {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(to bottom right, rgb(238, 114, 60), rgb(249, 150, 51));
  background: linear-gradient(to bottom right, var(--two), var(--three));
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: all 0.2s ease-in-out;
  -webkit-transform-origin: center;
          transform-origin: center;
  cursor: pointer;
}

.play-pause:hover {
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.play-pause-button {
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
  color: white;
  font-size: 90%;
  cursor: pointer;
}

.play-pause-button > i {
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
}

.progress-container {
  position: absolute;
  top: 60%;
  left: 60%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #dfdbdf;
  border-radius: 6px;
  cursor: pointer;
  height: 3px;
  width: 50%;
}

.progress {
  background-color: rgb(249, 150, 51);
  background-color: var(--three);
  border-radius: 6px;
  height: 100%;
  width: 80%;
  transition: width 0.1s linear;
}

.like-unlike {
  position: absolute;
  color: rgb(249, 150, 51);
  color: var(--three);
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  top: 10%;
  right: 10%;
}


